.nav_bar{
    display: flex;
    
    width: 100%;
    height: 11vh;
}
.tabs{
    display: flex;
    justify-content: space-around;
    gap: 15px;
    width: 75%;
    align-items: center;
    margin-right: 5px;
   

}
.nav_search{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25%;
   
    
    
   
    >input{
        border: 2px solid rgb(244, 184, 73) ;
       height: 35px;
        border-radius: 10px 0 0 10px;
        width: 60%;
        outline: none;
        padding: 0 8px;
        font-size: medium;
    }
    >span{
        background-color: rgb(244, 184, 73);
        border: 2px solid rgb(244, 184, 73) ;
        
        border-radius: 0 10px 10px 0;
        
           
    }
}
.toggle{
    border: none;
}